@media screen and (max-width: 768.98px) {
  .filter {
    margin-top: 5em; }
  .filter__product-title {
    font-weight: 600; }
  .filter__category {
    display: block;
    margin-bottom: 2em; }
  .filter__category-colection {
    display: none; }
  .filter__wrap-size {
    align-items: center;
    flex-wrap: nowrap;
    overflow: scroll; }
  .filter__wrap-size_sroll {
    display: flex; }
  .filter__wrap-size::-webkit-scrollbar {
    display: none; }
  .filter__category-btn-size {
    margin-bottom: 0 !important; }
  .filter__category-btn {
    margin-right: 0.4em;
    margin-bottom: 0.4em; }
  .filter__top-btn {
    margin-bottom: 2em; }
  .filter__product-title {
    max-width: 140px;
    margin-bottom: 1.5em; }
  .filter__wrap-size_sroll {
    flex-wrap: nowrap; }
  .range-slider {
    box-sizing: border-box;
    max-width: 375px;
    align-items: flex-start;
    flex-direction: column; }
  .number-group {
    max-width: 375px !important;
    width: 100%;
    height: auto !important;
    order: -1;
    /* margin-left: -17px; */
    margin-bottom: 2em;
    align-items: flex-start; }
  .input-wrap {
    position: relative;
    display: flex;
    align-items: center; }
  .multi-range-slider {
    max-width: 375px;
    width: 99%;
    margin: 0 !important; }
  input[type='range'] {
    max-width: 375px; }
  .range-slider .number-group .number-input {
    width: 130px;
    height: 48px; }
  .inputs__dush {
    top: -9px;
    left: 0;
    font-weight: 500;
    font-size: 36px;
    margin: 0; }
  .input__currency {
    left: 110px;
    top: 10px; }
  .input__currency_right {
    left: 110px; }
  .filter__sort-btn {
    white-space: nowrap;
    padding: 0; }
  .filter__category_mobile {
    display: flex;
    flex-direction: column; }
  .filter__category-discount {
    max-width: 375px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    order: -1; }
  .filter__category-discount > .filter__category-title {
    white-space: nowrap;
    display: inline;
    margin-bottom: 0; }
  .filter__category-discount > .filter__wrap {
    width: auto; }
  .toggler-wrapper.style-1 .toggler-knob {
    height: calc(25px - 4px);
    top: 2px; }
  .filter__category-sort {
    display: flex; }
  .filter__wrap-sort {
    max-width: 180px;
    width: 100%;
    display: grid;
    position: relative;
    max-height: 18.5px;
    overflow: hidden;
    justify-items: start; }
  .filter__sort-btn {
    width: 100%;
    text-align: left;
    z-index: 50; }
  .filter__wrap-sort_open {
    max-height: none; }
  .filter__wrap-sort-arrow {
    display: block;
    position: absolute;
    right: 0;
    top: 5px;
    transition: 0.4s ease-in-out; }
  .filter__wrap-sort-arrow_rotate {
    transform: rotate(180deg); }
  .filter__category-plus {
    display: block; }
  .slider {
    box-sizing: border-box;
    width: 91%;
    max-width: 350px;
    /* margin: 0 !important; */ }
  .slider > .track {
    width: 100%;
    left: 0px; } }

@media screen and (max-width: 375px) {
  .slider {
    width: 90%; } }

@media screen and (max-width: 374px) {
  .filter__product-title {
    max-width: 110px; }
  .slider {
    width: 89%; }
  /* .multi-range-slider {
    max-width: 275px;
  } */
  input[type='range'] {
    max-width: 270px; }
  /* .range-slider .number-group .number-input {
    width: 115px;
  } */
  .input__currency {
    left: 110px; }
  /* .input__currency_right {
    left: 276px;
  } */
  .inputs__dush {
    margin: 0em; } }
