.cards-list {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(305px, 1fr));
  -moz-column-gap: 0.42em;
       column-gap: 0.42em;
  row-gap: 1.42em; }

.cards-list__forever {
  row-gap: 3em; }

.card {
  max-width: 450px;
  width: 100%;
  /* height: 28.79em; */
  text-align: left; }

.card__body {
  position: relative;
  box-sizing: border-box;
  /* padding: 0.6em 0.97em 0.8em; */
  /* display: flex;
  flex-direction: column;
  align-items: center; */
  margin-bottom: 0.2em; }

.card__header {
  position: absolute;
  display: flex;
  width: 100%;
  justify-content: space-between;
  padding: 0.6em 0.97em 0.8em;
  box-sizing: border-box; }

.card__img {
  min-height: 100%;
  display: block;
  background-color: #fbfbfb;
  width: 100%;
  height: 100%;
  max-height: 100%;
  object-fit: cover;
  /* height: 23em; */ }

.card__img img {
  /* max-height: 345px; */
  width: 100%;
  background-color: #f3f3f3;
  max-height: 100%;
  width: 100%;
  object-fit: cover; }

.card__sale {
  font-family: 'Montserrat Regular';
  font-style: normal;
  font-weight: 500;
  font-size: 16px;
  line-height: 20px;
  color: #191919; }

.card__zoom {
  float: right; }

.card__zoom-btn {
  width: 1.71em;
  height: 1.71em;
  cursor: pointer;
  border: none;
  background-image: url(./static/card-zoom.svg);
  background-position: center center;
  background-repeat: no-repeat;
  transition: 0.3s ease-in-out;
  background-color: transparent; }

.card__zoom-btn:hover {
  transform: scale(1.11); }

.card__img-block {
  position: absolute;
  z-index: 0;
  width: 100%; }

.card__img-block img {
  width: 100%;
  height: 100%;
  object-fit: contain; }

.card__block-btn {
  width: 100%;
  position: absolute;
  bottom: 0;
  display: flex;
  padding: 0.6em 0.97em 0.8em;
  justify-content: end;
  box-sizing: border-box; }

@-webkit-keyframes ForeverBtn {
  0% {
    background-image: url(./static/card-heart.svg); }
  25% {
    background-image: url(./static/Heart2.svg); }
  50% {
    background-image: url(./static/Heart3.svg); }
  100% {
    background-image: url(./static/Heart4.svg); } }

@keyframes ForeverBtn {
  0% {
    background-image: url(./static/card-heart.svg); }
  25% {
    background-image: url(./static/Heart2.svg); }
  50% {
    background-image: url(./static/Heart3.svg); }
  100% {
    background-image: url(./static/Heart4.svg); } }

.card__forever-btn {
  width: 1.71em;
  height: 1.71em;
  cursor: pointer;
  border: none;
  background-image: url(./static/Heart1.svg);
  background-position: center center;
  background-repeat: no-repeat;
  transition: 0.6s ease-in-out;
  background-color: transparent;
  /* animation: ForeverBtn 0.5s ease-in-out reverse forwards; */ }

.card__forever-btn_active {
  -webkit-animation: ForeverBtn 0.5s ease-in-out forwards;
          animation: ForeverBtn 0.5s ease-in-out forwards; }

@media screen and (max-width: 768px) {
  .card__forever-btn_active {
    -webkit-animation: ForeverBtn 0.25s ease-in-out forwards;
            animation: ForeverBtn 0.25s ease-in-out forwards; } }

.card__name {
  font-family: 'Montserrat Medium';
  font-style: normal;
  font-weight: 600;
  font-size: 14px;
  line-height: 17px;
  color: #191919;
  margin-bottom: 0.2em; }

.card__price {
  font-family: 'Montserrat Regular';
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 20px;
  color: #191919; }

.card__price-forever {
  margin-bottom: 20px; }

.card__buy-btn {
  font-family: 'Montserrat Regular';
  font-style: normal;
  font-weight: 500;
  font-size: 16px;
  line-height: 20px;
  text-align: center;
  color: #fbfbfb;
  width: 100%;
  padding: 8px 0;
  background: #303437;
  border: 1px solid #191919;
  border-radius: 10px;
  transition: 0.3s ease-in-out;
  cursor: pointer; }

.card__buy-btn:hover {
  color: #191919;
  background-color: inherit;
  border: 1px solid #191919;
  border-radius: 10px; }

.card__buy-btn_mobile {
  width: 27px;
  height: 22px;
  position: relative;
  top: 38px;
  cursor: pointer;
  border: none;
  transition: 0.6s ease-in-out;
  background-color: transparent; }

.card__buy-btn_mobile {
  display: none; }

@media screen and (max-width: 768px) {
  .card__forever-btn_hidden {
    display: none; }
  .card__buy-btn_mobile {
    display: block; }
  .card__buy-btn {
    display: none; }
  .cards-list__forever {
    padding-top: 40px;
    row-gap: 0em; } }
