@media screen and (max-width: 768.98px) {
  footer {
    position: fixed;
    z-index: 500;
    bottom: 0px;
    width: 100%;
    padding-top: 17px;
    background: #fbfbfb; }
  .footer__body {
    height: auto; }
  .footer__desktop {
    display: none; }
  .footer__list {
    display: flex;
    margin: 0 auto; }
  .footer__mobile {
    box-sizing: border-box;
    display: block;
    padding: 0 2.6em;
    padding-bottom: 1.78em; }
  .footer__home-btn {
    text-decoration: none;
    color: #666; } }
