@media screen and (max-width: 375.98px) {
  .cards-list {
    grid-template-columns: repeat(auto-fit, minmax(8.42em, 1fr));
    row-gap: 0.5em; }
  .card {
    max-width: 11.42em; }
  .card__img img {
    max-height: 175px; }
  .card__body {
    margin-bottom: 0.4em; }
  .card__price {
    font-size: 14px; }
  .card__forever-btn {
    background-size: 1.2em 1.2em;
    top: 3em; } }
