.gift__wrap {
  padding-top: 29px;
  position: relative; }

.gift__title {
  font-family: 'Montserrat Medium';
  font-style: normal;
  font-weight: 600;
  font-size: 36px;
  line-height: 44px;
  text-align: center;
  color: #191919;
  margin-bottom: 3px; }

.gift__title-text {
  font-family: 'Montserrat Regular';
  font-style: normal;
  font-weight: 500;
  font-size: 20px;
  line-height: 24px;
  text-align: center;
  color: #191919; }

.gift-cards__block {
  padding-top: 47px;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(245px, 1fr));
  -moz-column-gap: 20px;
       column-gap: 20px;
  row-gap: 1.42em;
  padding-bottom: 258px; }

.gift .card__zoom-btn {
  position: absolute;
  right: 10.5px;
  top: 10.7px; }

.card {
  max-width: 450px;
  width: 100%;
  /* height: 28.79em; */
  text-align: left; }

.card__gift img {
  height: auto; }

.card__btn-list {
  display: flex;
  justify-content: space-between;
  padding-top: 7px;
  margin-bottom: 10px; }

.card__btn-list li button {
  width: 35px;
  height: 35px;
  border-radius: 100%;
  border: none;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer; }

.card__btn {
  font-family: 'Montserrat Medium';
  font-style: normal;
  font-weight: 500;
  font-size: 12px;
  line-height: 15px;
  text-align: center;
  color: #0e0e0e; }

.card__btn_active {
  color: #f0f0f0;
  background: #303437; }

.card__btn-list .card__btn_non-active {
  opacity: 0.3;
  cursor: auto; }

.card-btn__select {
  width: 100%;
  height: 36px;
  background: #303437;
  border-radius: 10px;
  border: none;
  font-family: 'Montserrat Regular';
  font-style: normal;
  font-weight: 500;
  font-size: 16px;
  line-height: 20px;
  text-align: center;
  color: #fbfbfb;
  cursor: pointer;
  transition: 0.3s ease-in-out; }

.card-btn__select:hover {
  border: 1px solid #303437;
  background-color: inherit;
  color: #303437; }

.gift-btn__next {
  max-width: 316px;
  width: 100%;
  height: 58px;
  background: #191919;
  border: none;
  border-radius: 34px;
  font-family: 'Montserrat Medium';
  font-style: normal;
  font-weight: 600;
  font-size: 24px;
  line-height: 29px;
  text-align: center;
  color: #fbfbfb;
  position: absolute;
  right: 0;
  bottom: 140px;
  cursor: pointer;
  transition: 0.3s ease-in-out; }

.gift-btn__next:hover {
  color: #191919;
  border: 1px solid #191919;
  background: inherit; }

@media screen and (max-width: 768px) {
  .gift__wrap {
    padding-top: 50px; } }

@media screen and (max-width: 375.98px) {
  .gift .card__gift {
    max-width: none; }
  .gift .card__gift .card__img img {
    max-height: none; } }
